
export const toDashName = title => title.replace(/\s+/g, '-');

export const toArray = (object = {}) => Object.values(object);

export const toQueryParams = object => 
  Object.keys(object).map(key => `${key}=${encodeURIComponent(object[key])}`).join('&');


// Just for 10 digit number validation (Indian phone without +91)
export const isMobileNumber = (value) => /^[0-9]{10}$/.test(value);

// Very simple email pattern validation eg anystring@anystring.ext
export const isSimpleEmail = (value) => /\S+@\S+\.\S+/.test(value);

export const isValidEmailOrPhone = (username) => {
  return !(isMobileNumber(username) || isSimpleEmail(username));
}