 /**
  * Custom Ajax wrapper for ease of use
  *  Ex:
  *  Ajax.post('/v1.0/auth/login')
  *    .headers({'X-username': 'Rajkeshwar'}) // Extra headers
  *    .data({ username, password })
  *    .fireRequest()
  *    .then(resp => console.log('Ajax login success', resp))
  */
class Ajax {

  // Converts from Array to object having key and value same
  static get method() {
    return ['GET', 'POST', 'PUT', 'DELETE']
      .reduce((acc, method) => ({...acc, [method]:method}), {});
  }

  constructor() {
    this._method = Ajax.method.GET;
    this._headers = {
      'Content-Type': 'application/json',
    };
  }

  get(url) {
    this._url = url;
    return this;
  }

  post(url) {
    this._url = url;
    this._method = Ajax.method.POST;
    return this;
  }

  put(url) {
    this._url = url;
    this._method = Ajax.method.PUT;
    return this;
  }

  delete(url) {
    this._url = url;
    this._method = Ajax.method.DELETE;
    return this;
  }

  data(data = {}) {
    this._data = data;
    return this;
  }

  headers(headers = {}) {
    this._headers = {...this._headers, ...headers};
    return this;
  }

  fireRequest() {
    const {_method: method, _headers: headers} = this;
    return fetch(this._url, {
      method, headers, body: JSON.stringify(this._data),
    })
    .then(resp => resp.json())
    .catch(err => console.warn('Ajax error ', err));
  }
}

export default new Ajax();