import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import AccountPage from '../pages/AccountPage';
import SavedCards from './SavedCards';
import WishlistCard from './WishlistCard';
import ManageAddresses from './ManageAddresses';
import ProfileInformation from './ProfileInformation';
import { Link, Route } from 'react-router-dom';
import RewardsCard from './RewardsCard';
import MyReviewsAndRatings from './MyReviewsAndRatings';
import MyCoupons from './MyCoupons';

const useStyles = makeStyles(theme => ({
  active: {
    background: '#f5faff',
  },
  primary: {
    color: theme.palette.primary.main,
  },
  noClicks: {
    pointerEvents: 'none'
  }
}))

const navList = [
  {label: 'MY ORDERS', inset: false, children: []},
  {label: 'Account Settings', inset: false, children: [
    {label: 'Profile Information', inset: true, route: '/account', active: true },
    {label: 'Manage Addresses', inset: true, route: '/account/addresses' },
  ]},
  {label: 'PAYMENTS', inset: false, children: [
    {label: 'Gift Cards', inset: true, route: '/account/rewards' },
    {label: 'Saved Cards', inset: true, route: '/account/savedcards' },
  ]},
  {label: 'MY STUFF', inset: false, children: [
    {label: 'My Coupons', inset: true, route: '/account/coupons' },
    {label: 'My Wishlist', inset: true, route: '/account/wishlist' },
    {label: 'My Reviews & Ratings', inset: true, route: '/account/ratings' },
  ]},
  {label: 'Logout', inset: false, children: []},
]

export const routes = [
  {path: '/account', component: <ProfileInformation/>},
  {path: '/account/addresses', component: <ManageAddresses/>},
  {path: '/account/wishlist', component: <WishlistCard/>},
  {path: '/account/savedcards', component: <SavedCards/>},
  {path: '/account/rewards', component: <RewardsCard/>},
  {path: '/account/ratings', component: <MyReviewsAndRatings/>},
  {path: '/account/coupons', component: <MyCoupons/>},
]

const GroupNavItem = ({children, divider, ...props}) => {
  return (
    <>
      <ChildNavItem {...props}>
        <ListItemIcon>
          <InboxIcon />
        </ListItemIcon>
      </ChildNavItem>
      {children}
      {divider ? <Divider /> : ''}
    </>
  )
}

const ChildNavItem = ({label, children, active, inset, route}) => {
  const clz = useStyles()
  const primary = active ? {primary: clz.primary} : {}
  const background = active ? {root: clz.active} : {root: route ? null: clz.noClicks}

  const routeTo = ({pathname}) => {
    return route ? route : pathname;
  }

  return (
    <ListItem button classes={background} component={Link} to={routeTo}>
      {children}
      <ListItemText inset={inset} primary={label} classes={primary}/>
    </ListItem>
  )
}

const AccountSideNav = () => {
  return (
    <List component="nav" aria-label="Accounts">
      {navList.map((item, idx) => (
        <GroupNavItem 
          key={idx} 
          {...item} 
          divider={idx !== navList.length - 1}>
          {item.children.map((child, cidx) => (
            <ChildNavItem key={cidx} {...child}/>
          ))}
        </GroupNavItem>
      ))}
    </List>
  );
}
 
export default AccountSideNav;