import React from 'react';
import ProductList from '../core/ProductList';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import {ProductLabel, ProductRating, ProductPrice, ExtraDiscount} from '../core/ProductFeatures';
import {ProductImage} from '../core/ProductList';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  root: {

  },
  header: {
    padding: `${theme.spacing(2)}px ${theme.spacing(4)}px`,
    borderBottom: theme.palette.border,
  },
  wishlist: {
    paddingTop: theme.spacing(1),
  },
  wishlistItem: {
    padding: `${theme.spacing(2)}px 0`,
    borderBottom: theme.palette.border,
  }
}), {name: 'WishlistCard'})

const wishlistItems = [
  'cellphone', 'laptop', 'smartwatch'
]

const WishlistCard = () => {
  const clz = useStyles()

  return (
    <div className={clz.root}>
      <div className={clz.header}>
        <Typography variant="h6">My Wishlist (5)</Typography>
      </div>
      <div className={clz.wishlist}>
        {wishlistItems.map((item, idx) => (
          <div key={idx} className={clz.wishlistItem}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={2}>
                <ProductImage backgroundImage="url(images/slide-li.jpeg)" height="80"/>
              </Grid>
              <Grid item xs={12} sm={10}>
                <ProductLabel/>
                <ProductRating/>
                <ExtraDiscount/>
                <ProductPrice/>
              </Grid>
            </Grid>
          </div>
        ))}
      </div>
    </div>
  );
}
 
export default WishlistCard;