import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Card, CardActionArea, CardContent, CardMedia, Button, Typography, CardActions } from '@material-ui/core';
import { Link, useRouteMatch } from 'react-router-dom';
import { toDashName } from '../utils/utils';

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: `calc(20% - ${theme.spacing(1)}px)`,
    boxSizing: 'border-box',
    marginRight: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  anchor: {
    color: 'inherit',
    textDecoration: 'none',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',

    '&>button': {
      flex: 1,
    }
  }
}), {name: ProductCard.name});

export default function ProductCard({ landing_page_image, display_name, long_description }) {
  const clz = useStyles();
  const { url } = useRouteMatch();
  const name = toDashName(display_name);

  return (
    <Card className={clz.root}>
      <Link className={clz.anchor} to={`${url}/${name}`}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt={display_name}
          height="200"
          image={landing_page_image}
          title={display_name}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {display_name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {long_description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Available
        </Button>
        
        <Button size="small" color="secondary">
          Not Available
        </Button>
      </CardActions>
      </Link>
    </Card>
  );
}