import React, {useEffect, useState} from 'react';
import ProductContainer from '../ProductContainer';
import { toArray, toQueryParams } from '../utils/utils';

const ProductsPage = () => {
  const [productList, setProductList] = useState([]);
  const [filters, setFilters] = useState([]);

  const setProductsAndFilters = (resp) => {
    const filters = toArray(resp.slice(-1)[0].filters || {});
    setProductList(resp.slice(0, -1));
    setFilters(filters);
  }

  useEffect(() => {
    console.log('useEffect is called ')

    const queryParams = toQueryParams({
      cat_id: 'PRODUCT-0642363',
      catalog_id: 'cat#cloth_001'
    })

    fetch(`/v1.0/products/catproductlist?${queryParams}`)
      .then(resp => resp.json())
      .then(resp => resp.value)
      .then(resp => toArray(resp))
      .then(resp => setProductsAndFilters(resp))
  }, [])

  return (
    <>
      <ProductContainer filters={filters} products={productList}/>
    </>
  );
}
 
export default ProductsPage;