import React from 'react';
import { useHistory, useLocation, Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Badge from '@material-ui/core/Badge';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Typography from '@material-ui/core/Typography';
import MoreIcon from '@material-ui/icons/MoreVert';
import Button from '@material-ui/core/Button';
import LoginModal from '../components/LoginModal';
import ProductSearchBar from '../core/ProductSearchBar';
import P5OnlineLogo from '../core/P5OnlineLogo';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import { ExpanderIcon } from '../core/Icons';
import { useStore } from '../store/store';
import Ajax from '../utils/Ajax';
import { observer } from 'mobx-react-lite';

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    width: theme.sidebar.width - 64,
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    display: 'flex',
    alignItems: 'center',
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  logo: {
    height: '60px',
    width: '200px',
    marginRight: theme.spacing(2),

    '&>span': {
      display: 'table',
      
      '&:hover': {
        cursor: 'pointer'
      },
      '&>svg': {
        height: '100% !important',
        width: '100% !important',
      },

    }
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
  loginBtn: {
    ...theme.flex.center,
    '&>*': {
      margin: '0 8px',
    }
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  whishlist: {
    justifyContent: 'flex-end',
    paddingRight: theme.spacing(1),
    boxSizing: 'border-box',
  },
}));
 
function HeaderNavBar() {
  const clz = useStyles();
  const {userStore, alertStore} = useStore()

  const [loginProgress, setLoginPgrogress] = React.useState(false);
  const [userAnchorEl, setUserAnchorEl] = React.useState(null);
  const [moreAnchorEl, setMoreAnchorEl] = React.useState(null);
  
  const mobileMenuId = 'primary-search-account-menu-mobile';

  let history = useHistory();
  let location = useLocation();

  let { from } = location.state || { from: { pathname: "/" } };
  let routeHome = () => history.replace(from);

  const handleLogout = () => {
    Ajax.post('/v1.0/auth/logout')
      .headers({'Authorization': `Bearer ${userStore.accessToken}`})
      .fireRequest()
      .then(response => {
        userStore.clearStorage()
        setUserAnchorEl(null)
        alertStore.setToast({
          message: response.message,
          isOpen: true
        })
      })
  }

  return (
    <>
      <div className={clz.grow}>
        <AppBar position="fixed">
          <Toolbar>
            <IconButton
              edge="start"
              className={clz.menuButton}
              color="inherit"
              aria-label="open drawer"
            >
              <MenuIcon />
            </IconButton>
            <div className={clz.logo}>
              <span onClick={routeHome}>
                <P5OnlineLogo />
              </span>
            </div>
            <div className={clz.search}>
              <ProductSearchBar placeholder="Search for products, brands and more"/>
            </div>
            <div className={clz.grow} />
            
            <div className={clz.sectionDesktop}>
              <div className={clz.loginBtn}>
                <Button variant="contained" color="secondary" 
                  hidden={userStore.isLoggedIn}
                  startIcon={<AccountCircle/>} 
                  aria-controls="user-menu" 
                  aria-haspopup="true"
                  onClick={e => setLoginPgrogress(true)}>Login
                </Button>
                <Button color="primary" color="inherit" aria-label="Rajkeshwar" 
                  hidden={!userStore.isLoggedIn}
                  onMouseEnter={e => setUserAnchorEl(e.currentTarget)}>
                  {userStore.username} <ExpanderIcon isExpanded={userAnchorEl}/> 
                </Button>
                <Menu className={clz.profileMenu}
                  id="user-profile-menu"
                  anchorEl={userAnchorEl}
                  open={Boolean(userAnchorEl)}
                  onClose={e => setUserAnchorEl(null)}
                  getContentAnchorEl={null}
                  anchorOrigin={{vertical: 'bottom', horizontal: 'left'}}
                >
                  <MenuItem>My Profile</MenuItem>
                  <MenuItem>Orders</MenuItem>
                  <MenuItem>
                    <Typography variant="inherit">Wishlist</Typography>
                    <ListItemIcon classes={{root: clz.whishlist}}>
                      <Badge badgeContent={4} color="primary"/>
                    </ListItemIcon>
                  </MenuItem>
                  <MenuItem>Coupons</MenuItem>
                  <MenuItem>Gift Cards</MenuItem>
                  <MenuItem>Notifications</MenuItem>
                  <MenuItem onClick={handleLogout}>Logout</MenuItem>
                </Menu>
                <Button color="inherit" aria-label="More" 
                  onMouseEnter={e => setMoreAnchorEl(e.currentTarget)}>
                  More <ExpanderIcon isExpanded={moreAnchorEl}/> 
                </Button>
                <Menu
                  id="user-more-menu"
                  anchorEl={moreAnchorEl}
                  open={Boolean(moreAnchorEl)}
                  onClose={e => setMoreAnchorEl(null)}
                  getContentAnchorEl={null}
                  anchorOrigin={{vertical: 'bottom', horizontal: 'left'}}
                >
                  <MenuItem component={Link} to="/account">My Account</MenuItem>
                  <MenuItem>Notifications</MenuItem>
                  <MenuItem>Sell on P5Online</MenuItem>
                  <MenuItem>Download App</MenuItem>
                </Menu>
                <IconButton aria-label="add to shopping cart">
                  <Link to="/viewcart">
                    <Badge badgeContent={4} color="error">
                      <AddShoppingCartIcon htmlColor="#fff"/> 
                    </Badge>
                  </Link>
                </IconButton>
              </div>
              
            </div>
            <div className={clz.sectionMobile}>
              <IconButton
                aria-label="show more"
                aria-controls={mobileMenuId}
                aria-haspopup="true"
                color="inherit"
              >
                <MoreIcon />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
      </div>
      <LoginModal isOpen={loginProgress} setIsOpen={e => setLoginPgrogress(false)}/>
    </>
  );
}

export default observer(HeaderNavBar)