import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Icon from '@material-ui/core/Icon';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles(theme => ({
  formWpr: {
    padding: theme.spacing(1),
    width: '100%',
    boxSizing: 'border-box',
  },
  add: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    border: theme.palette.border,
    padding: theme.spacing(1),
    marginBottom: theme.spacing(4),
  },
  highlight: {
    background: theme.color.form,
  },
  btnGroup: {
    display: 'flex',
    '&>button': {
      marginRight: theme.spacing(1),
      minWidth: 200
    }
  },
}), {name: 'AddNewFormWrapper'})

function noop() {}

const AddNewFormWrapper = ({addBtnText, saveBtnText='Save', onSave=noop, onClose=noop, children}) => {
  const clz = useStyles()
  const [formVisible, setFormVisible] = React.useState(false)

  const handleClose = (e) => {
    setFormVisible(false)
    onClose(e)
  }

  const handleSave = (e) => {
    setFormVisible(false)
    onSave(e)
  }

  return (
    <div className={clsx(clz.add, {[clz.highlight]: formVisible})}>
      <Button color="primary" onClick={e => setFormVisible(true)}>
        <Icon style={{ marginRight: 8 }}>add</Icon>
        {addBtnText}
      </Button>
      <div className={clz.formWpr} hidden={!formVisible}>
        <form className={clz.form} noValidate autoComplete="off">
          {children}
          <div className={clz.btnGroup}>
            <Button size="large" variant="contained" color="primary" onClick={handleSave}>{saveBtnText}</Button>
            <Button size="large" onClick={handleClose}>Cancel</Button>
          </div>
        </form>
      </div>
    </div>
  );
}
 
export default AddNewFormWrapper;