
import { action, decorate, observable } from 'mobx'

class BugsStore {
  
  constructor() {
    this.bugs = ['Centipede']
  }
  
  setBug(newBug) {
    this.bugs.push(newBug)
  }

  get bugsCount() {
    return this.bugs.length
  }

  getAll() {
    return this.bugs
  }
}

decorate(BugsStore, {
  bugs: observable,
  setBug: action,
  getAll: action
});

export default BugsStore;