import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Link from '@material-ui/core/Link';
import Alert from '@material-ui/lab/Alert';
import Backdrop from '@material-ui/core/Backdrop';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Fade from '@material-ui/core/Fade';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import Ajax from '../utils/Ajax';
import { isMobileNumber, isValidEmailOrPhone } from '../utils/utils';
import { useStore } from '../store/store';

const useStyles = makeStyles((theme) => ({
  modal: {
    ...theme.flex.center,
  },
  paper: {
    ...theme.flex.column,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(3, 6),
    height: 400,
    width: '60%',
    maxWidth: '600px',
    outline: 'none',
  },
  header: {
    position: 'relative',
  },
  row: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: theme.spacing(1),
  },
  alert: {
    ...theme.flex.center,
    alignItems: 'flex-start',
    flex: 1,
    '&>*': {
      padding: `0 ${theme.spacing(2)}px`,
    }
  },
  form: {
    height: '100%',
    ...theme.flex.column
  },
  body: {
    height: '100%',
    ...theme.flex.column,
  },
  formControl: {
    marginBottom: theme.spacing(3), 
  },
  close: {
    position: 'absolute',
    top: theme.spacing(1),
    right: theme.spacing(1),

    '&:hover': {
      cursor: 'pointer',
    }
  },
  loginOrOtp: {
    display: 'flex',
    flexDirection: 'column',
    margin: `${theme.spacing(1)}px 0`,
  },
  or: {
    display: 'flex',
    justifyContent: 'center',
    margin: theme.spacing(1.5),
  },
  spacer: {
    flex: 1,
    '&+h6:hover': {
      cursor: 'pointer',
    }
  },
}), {name: 'LoginModal'});

const composeUser = (formValues) => {
  const {username, password} = formValues;
  const phoneOrEmail = isMobileNumber(username) ? 'phone' : 'email';
  return { [phoneOrEmail]:username, password };
};

const UserLoginForm = ({onLoginResponse}) => {
  const clz = useStyles();
  const {userStore} = useStore()

  const [formValues, setFormValues] = React.useState({
    username: '', 
    password: '',
  });
  
  const handleChange = (prop) => (event) => {
    setFormValues({ ...formValues, [prop]: event.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Temporary hack till API returns user data on login
    userStore.set('username', formValues.username)

    Ajax.post('/v1.0/auth/login')
      .data(composeUser(formValues))
      .fireRequest()
      .then(onLoginResponse);
  };

  return (
    <>
      <form className={clz.form} noValidate autoComplete="off" onSubmit={handleSubmit}>
        <FormControl className={clz.formControl}>
          <TextField onChange={handleChange('username')} label="Username" />
        </FormControl>
        <FormControl className={clz.formControl}>
          <PasswordField onChange={handleChange('password')}/>
        </FormControl>
        <div className={clz.loginOrOtp}>
          <Button variant="contained" type="submit" color="primary">Login</Button>
          <div className={clz.or}>OR</div>
          <Button variant="contained">Request OTP</Button>
        </div>
      </form>
    </>
  );
}
 
const Highlight = ({children}) => {
  return (<Link href="#" onClick={(e) => e.preventDefault()} variant="body2">{children}</Link>);
}
 
const UserSignUpForm = ({onSignupResponse}) => {
  const clz = useStyles();

  const [formValues, setFormValues] = React.useState({
    username: '',
    password: '',
    usernameError: false,
    passwordError: false,
  });
  
  const handleChange = (prop) => (event) => {
    setFormValues({ ...formValues, [prop]: event.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Reset all form error fields to validate again
    setFormValues({ ...formValues, usernameError: false, passwordError: false });

    if (isValidEmailOrPhone(formValues.username)) {
      setFormValues({ ...formValues, usernameError: true });
      return;
    }

    // Password complexity not available yet to validate

    Ajax.post('/v1.0/auth/register')
      .data(composeUser(formValues))
      .fireRequest()
      .then(onSignupResponse);
  };

  return (
    <form className={clz.form} onSubmit={handleSubmit} noValidate autoComplete="off">
      <FormControl className={clz.formControl}>
        <TextField label="Email or Phone" 
          error={formValues.usernameError} 
          helperText={formValues.usernameError ? 'Invalid email or phone' : ''} 
          onChange={handleChange('username')}/>
      </FormControl>
      <FormControl className={clz.formControl}>
        <PasswordField onChange={handleChange('password')}/>
        {/* <TextField label="Password" 
        error={formValues.passwordError} 
        helperText={formValues.passwordError ? 'Does not meet the password policy requirements' : ''}
        onChange={handleChange('password')}/> */}
      </FormControl>
      <div className={clz.spacer}></div>
      <Typography variant="caption" display="block" gutterBottom>
        By continuing, you agree to P5Online's 
        <Highlight>Terms of Use</Highlight> and <Highlight>Privacy Policy.</Highlight>
      </Typography>
      <FormControl className={clz.formControl}>
        <div className={clz.loginOrOtp}>
          <Button variant="contained" type="submit" color="primary">Continue</Button>
        </div>
      </FormControl>
    </form>
  );
}

const PasswordField = ({onChange}) => {
  const [values, setValues] = React.useState({
    amount: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });
  
  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
    onChange(event);
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  return (
    <>
      <InputLabel htmlFor="standard-adornment-password">Password</InputLabel>
      <Input
        id="standard-adornment-password"
        type={values.showPassword ? 'text' : 'password'}
        value={values.password}
        onChange={handleChange('password')}
        endAdornment={
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
            >
              {values.showPassword ? <Visibility /> : <VisibilityOff />}
            </IconButton>
          </InputAdornment>
        }
      />
    </>
  );
}
 
export default function LoginModal({isOpen, setIsOpen}) {
  const clz = useStyles();
  const {userStore, alertStore} = useStore()
  const [isLogin, setIsLogin] = React.useState(true);

  const [alert, setAlert] = React.useState({
    isVisible: false,
    severity: 'info',
    message: '',
  });

  const handleLoginResponse = ({message, value: user}) => {
    if(message === 'LOGGEDIN') {
      Object.entries(user.token)
        .forEach(([key, value]) => userStore.set(key, value));
        userStore.isLoggedIn = true // Update loggedIn state
        setTimeout(() => setIsOpen(false), 100) // forceUpdate
        alertStore.setToast({
          message: `${userStore.username} logged in`,
          isOpen: true
        })
    } else {
      setAlert({...alert, isVisible: true, severity: 'error', message});
      setTimeout(_ =>  setAlert({...alert, isVisible: false}), 3000);
    }
  };

  const handleSignupResponse = ({code, message}) => {
    setTimeout(_ =>  setAlert({...alert, isVisible: false}), 3000);
    if (code) { 
      setAlert({...alert, isVisible: true, severity: 'error', message});
    } else {
      setAlert({...alert, isVisible: true, severity: 'success', message});
    }
  };

  return (
    <div>
      <Modal
        className={clz.modal}
        open={isOpen}
        onClose={setIsOpen}
        closeAfterTransition
        disableBackdropClick
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={isOpen}>
          <div className={clz.paper}>
            <span className={clz.close} onClick={setIsOpen}>
              <IconButton aria-label="close">
                <CloseIcon/>
              </IconButton>
            </span>

            <div className={clz.header}>
              <div className={clz.row}>
                <Typography variant="h4" gutterBottom>{isLogin ? 'Login' : 'Sign Up'}</Typography>
                <div className={clz.alert}>
                  <Fade in={alert.isVisible}>
                    <Alert severity={alert.severity}>{alert.message}</Alert>
                  </Fade>
                </div>
              </div>
              <Typography variant="body2" gutterBottom>
                {isLogin 
                  ? 'Get access to your Orders, Wishlist and Recommendations' 
                  : 'Sign up with your mobile number to get started'
                }
              </Typography>
            </div>
            <div className={clz.body}>
              {isLogin 
                ? <UserLoginForm onLoginResponse={handleLoginResponse}/> 
                : <UserSignUpForm onSignupResponse={handleSignupResponse}/>
              }
            </div>
            <div className={clz.spacer}></div>
            <Typography variant="subtitle2" color="primary" align="center" onClick={() => setIsLogin(!isLogin)}>
              {isLogin 
                ? <span>New to P5Online? Create an account</span>
                : <span>Already have an account? Sign-In</span>
              }
            </Typography>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}