
import { action, decorate, observable } from 'mobx'

class AlertStore {
  
  constructor() {
    this.toast = {
      isOpen: false,
      severity: 'success',
      message: '',
      duration: 5000
    }
  }
  
  setToast(options) {
    for(let key in options) {
      if (options.hasOwnProperty(key)) {
        this.toast[key] = options[key]
      }
    }
  }

  _autohide() {
    if (this.__timeout) {
      clearTimeout(this.__timeout)
    }

    this.__timeout = setTimeout(() => this.toast.isOpen = false, this.duration)
  }
}

decorate(AlertStore, {
  toast: observable,
  setToast: action,
});

export default AlertStore;