
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  expander: {
    display: 'flex',
    justifyContent: 'center',
    width: 20
  },
  expanded: {
    display: 'inline-block',
    cursor: 'pointer',
    width: 6,
    height: 11,
  },
}), {name: 'ExpanderIcon'});

export const ExpanderIcon = ({ onClick = () => {}, isExpanded, color }) => {
  const clz = useStyles();
  const fill = color ? color : 'white';

  return (
    <div className={clz.expander} onClick={onClick}>
      <svg width="16" height="27" viewBox="0 0 16 27"
        xmlns="http://www.w3.org/2000/svg" className={clz.expanded}
        style={{transform: isExpanded ? 'rotate(90deg)' : 'rotate(-90deg)'}}>
        <path d="M16 23.207L6.11 13.161 16 3.093 12.955 0 0 13.161l12.955 13.161z" fill={fill}></path>
      </svg>
    </div>
  )
}