import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {ProductLabel, ProductRating, SoldOut, ProductPrice, ExtraDiscount} from './ProductFeatures';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';


const useStyles = makeStyles(theme => ({
  wpr: {
    padding: `${theme.spacing(2)}px 0`,
  },
  imageContainer: {
    ...theme.flex.center,
    position: 'relative',
    height: '100%',
  },
  bgImage: {
    width: '100%',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '50%',
    backgroundSize: 'contain',
  },
  save: {
    display: 'flex',
    padding: `${theme.spacing(1)}px 0`,
  }
}))

export const ProductImage = ({backgroundImage, height=120}) => {
  const clz = useStyles()
  return (
    <div className={clz.imageContainer}>
      <div className={clz.bgImage} style={{backgroundImage: backgroundImage, height:`${height}px`}}></div>
    </div>
  );
}

const SaveForLater = () => {
  const clz = useStyles()

  return (
    <div className={clz.save}>
      <Button>Save for later</Button>
      <Button>Remove</Button>
    </div>
  );
}

const ProductList = () => {
  const clz = useStyles()

  return (
    <div className={clz.wpr}>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={3}>
          <ProductImage backgroundImage="url(images/slide-li.jpeg)"/>
        </Grid>
        <Grid item xs={12} sm={9}>
          <ProductLabel/>
          <ProductRating/>
          <ExtraDiscount/>
          <ProductPrice/>
          <SaveForLater/>
        </Grid>
      </Grid>
    </div>
  );
}
 
export default ProductList;