import React from 'react'
import { observer } from 'mobx-react-lite'
import UserStore from './userStore';
import BugsStore from './bugsStore';
import AlertStore from './alertStore';

const store = {
  userStore: new UserStore(),
  bugsStore: new BugsStore(),
  alertStore: new AlertStore(),
}

export const StoreProvider = ({ children }) => {
  return (
    <StoreContext.Provider value={store}>
      {children}
    </StoreContext.Provider>
  )
}

const StoreContext = React.createContext(store);

export const useStore = () => {
  const store = React.useContext(StoreContext);
  if (!store) {
    throw new Error('useStore must be used within a StoreProvider.');
  }
  return store;
};


const BugForm = () => {
  const [bug, setBug] = React.useState('')
  const {bugsStore} = useStore()

  return (
    <form onSubmit={e => {
      e.preventDefault()
      bugsStore.setBug(bug)
      setBug('')
    }}>
      <input type="text" value={bug} onChange={e => setBug(e.target.value)}/>
      <input type="submit" value="Add"/>
    </form>
  )
}

export const BugsList = observer(() => {
  const {bugsStore} = useStore()

  return (
    <ul>
      {bugsStore.bugs.map((bug, i) => (
        <li key={i}>{bug}</li>
      ))}
      <BugForm/>
    </ul>
  )
})