import React, { useEffect, useState } from 'react';
import { MuiThemeProvider } from '@material-ui/core/styles'
import { observer } from 'mobx-react-lite';
import {
  HashRouter,
  Switch,
  Route,
} from 'react-router-dom';

import './App.css';
import HeaderNavBar from './header/HeaderNavBar';
import theme from './theme';
import LandingPage from './pages/LandingPage';
import ProductsPage from './pages/ProductsPage';
import ProductsDetailPage from './pages/ProductDetailPage';
import ViewCartPage from './pages/ViewCartPage';
import CategoryBar from './header/CategoryBar';
import { toArray } from './utils/utils';
import Toaster from './core/Toaster';
import { StoreProvider, BugsList, useStore } from './store/store';
import { MyProfileRoutes, routes } from './components/AccountSideNav';
import AccountPage from './pages/AccountPage';

const NoMatchFound = () => {
  return ( 
    <>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    <h3>No match for</h3>
    </>
  );
}
 
function App() {
  const [tabs, setTabs] = useState([]);
  const {alertStore} = useStore()

  useEffect(() => {
    fetch(`/v1.0/products/tabs`)
      .then(resp => resp.json())
      .then(resp => toArray(resp))
      .then(resp => setTabs([...resp]))
  }, [])

  return (
    <>
      <MuiThemeProvider theme={theme}>
        <StoreProvider>
          <Toaster 
            open={alertStore.toast.isOpen} 
            severity={alertStore.toast.severity}>
            {alertStore.toast.message}
          </Toaster>
          {/* <BugsList/> */}
          <HashRouter basename='/'>
            <Switch>
              <Route exact path="/">
                <HeaderNavBar/>
                <CategoryBar tabs={tabs}/>
                <LandingPage/>
              </Route>
              <Route exact path="/viewcart">
                <HeaderNavBar/>
                <ViewCartPage/>
              </Route>
              {routes.map(({path, component}) => (
                <Route exact key={path} path={path}>
                  <AccountPage>{component}</AccountPage>
                </Route>
              ))}
              <Route exact path="/:productId">
                <HeaderNavBar/>
                <CategoryBar tabs={tabs}/>
                <ProductsPage/>
              </Route>
              <Route path="/:productId/:details">
                <HeaderNavBar/>
                <CategoryBar tabs={tabs}/>
                <ProductsDetailPage/>
              </Route>
              <Route path="*">
                <NoMatchFound />
              </Route>
            </Switch>
          </HashRouter>
        </StoreProvider>
      </MuiThemeProvider>
    </>
  );
}

export default observer(App);
